
        *{
            margin:0;padding:0; box-sizing: border-box;
        }
        *::before{box-sizing: border-box;}
        *::after{box-sizing: border-box;}
        #demo{
            word-break: break-all; 
        }
        #div1{
            position:fixed;
            left: 50%;
            transform: translateX(-50%);
            top:20px;
        }
        #div1::before{
        content: '';
        display:block;
        position:absolute;
        }
        #div1::after{
        content: '';
        display:block;
        position:absolute;
        }
        @media(max-width: 500px){
            #demo{
                height:50vh;
                overflow: auto;
            }
            #divWrapper{
                height:50vh;
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, 0);
                padding-top: 30px;

            }
            #div1{
                position:relative;
                top:0;
                right:0;
            }
        }
